@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', system-ui, sans-serif;
  }

  body {
    @apply bg-gray-50 text-gray-900;
  }
}

@layer components {
  .sidebar-item {
    @apply flex items-center px-4 py-3 text-gray-700 hover:bg-gray-100 hover:text-primary-600 transition-colors duration-200;
  }

  .sidebar-item.active {
    @apply bg-primary-50 text-primary-600 border-r-2 border-primary-600;
  }

  .card {
    @apply bg-white rounded-lg shadow-soft border border-gray-200;
  }

  .btn-primary {
    @apply bg-primary-600 hover:bg-primary-700 text-white font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }

  .btn-secondary {
    @apply bg-gray-100 hover:bg-gray-200 text-gray-700 font-medium py-2 px-4 rounded-lg transition-colors duration-200;
  }

  /* Running status animation */
  .status-running {
    @apply text-blue-600 bg-blue-100;
    animation: pulse-running 2s ease-in-out infinite;
  }

  @keyframes pulse-running {
    0%,
    100% {
      opacity: 1;
      transform: scale(1);
    }
    50% {
      opacity: 0.8;
      transform: scale(1.02);
    }
  }

  /* Progress bar animation for running jobs */
  .progress-running {
    animation: progress-pulse 1.5s ease-in-out infinite;
  }

  @keyframes progress-pulse {
    0%,
    100% {
      opacity: 1;
    }
    50% {
      opacity: 0.7;
    }
  }

  /* Running jobs count animation */
  .running-count {
    animation: running-glow 3s ease-in-out infinite;
  }

  @keyframes running-glow {
    0%,
    100% {
      text-shadow: none;
    }
    50% {
      text-shadow: 0 0 8px rgba(59, 130, 246, 0.5);
    }
  }

  /* Animated progress bar for running jobs */
  .progress-bar-animated {
    position: relative;
    overflow: hidden;
  }

  .progress-bar-animated::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.4),
      transparent
    );
    animation: progress-shimmer 2s ease-in-out infinite;
  }

  @keyframes progress-shimmer {
    0% {
      left: -100%;
    }
    100% {
      left: 100%;
    }
  }

  /* Pulsing progress bar for running jobs */
  .progress-bar-pulse {
    animation: progress-fill 3s ease-in-out infinite;
  }

  @keyframes progress-fill {
    0%,
    100% {
      transform: scaleX(1);
      opacity: 1;
    }
    50% {
      transform: scaleX(1.02);
      opacity: 0.9;
    }
  }
}
